import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { HelperHistoricmealPage } from 'src/app/modals/helper-historicmeal/helper-historicmeal.page';
import { HelperRegistermealPage } from 'src/app/modals/helper-registermeal/helper-registermeal.page';
import { HelperSupportcenterPage } from 'src/app/modals/helper-supportcenter/helper-supportcenter.page';
import { HelperSupportnetworkPage } from 'src/app/modals/helper-supportnetwork/helper-supportnetwork.page';
import { HelperTipsPage } from 'src/app/modals/helper-tips/helper-tips.page';
import { HelperDailydishPage } from 'src/app/modals/helper-dailydish/helper-dailydish.page';

@Component({
  selector: 'app-helper',
  templateUrl: './helper.page.html',
  styleUrls: ['./helper.page.scss'],
})
export class HelperPage implements OnInit {

  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {
  }

  async HelperHistoricmealPage() {
    const modal = await this.modalCtrl.create({
      component: HelperHistoricmealPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }

  async HelperRegistermealPage() {
    const modal = await this.modalCtrl.create({
      component: HelperRegistermealPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }

  async HelperSupportcenterPage() {
    const modal = await this.modalCtrl.create({
      component: HelperSupportcenterPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }

  async HelperSupportnetworkPage() {
    const modal = await this.modalCtrl.create({
      component: HelperSupportnetworkPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }

  async HelperTipsPage() {
    const modal = await this.modalCtrl.create({
      component: HelperTipsPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }

  async HelperDailydishPage() {
    const modal = await this.modalCtrl.create({
      component: HelperDailydishPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }

}
